

































import Vue from 'vue';
import Navbar from '@/components/Navbar.vue';
import MyFooter from '@/components/MyFooter.vue';
import Timer from '@/components/Timer.vue';
export default Vue.extend({
  name: 'MainLayout',
  components: {
    Navbar,
    MyFooter,
    Timer,
  },
  data() {
    return {
      showTimer: true,
      timerIsActive: false,
    };
  },
  methods: {
    switchTimer() {
      this.$data.showTimer = !this.$data.showTimer;
    },
  },
});
