
















import Vue from 'vue';
import DropdawnSelect from '@/components/DropdawnSelect.vue';

export default Vue.extend({
  components: {
    DropdawnSelect,
  },
  name: 'Timer',
  props: {},
  data() {
    return {
      period: 0,
      timerSeconds: '00',
      timerMinutes: '00',
      timerIsActive: false,
    };
  },
  methods: {
    isSelected(value: number) {
      if (!this.$data.timerIsActive) {
        this.$data.period = value;
      }
    },
    startCount() {
      if (this.period === 0 || this.$data.timerIsActive) {
        return;
      }
      this.$emit('timerOn');
      this.$data.timerIsActive = true;
      let innerTimer = 1;

      const theTimer = setInterval(() => {
        const innerSeconds = innerTimer % 60;
        const innerMinutes = Math.floor(innerTimer / 60);
        this.$data.timerSeconds =
          innerSeconds < 10 ? '0' + innerSeconds : innerSeconds;
        this.$data.timerMinutes =
          innerMinutes < 10 ? '0' + innerMinutes : innerMinutes;

        if (innerTimer === parseInt(this.$data.period, 10)) {
          this.$data.timerSeconds = '00';
          this.$data.timerMinutes = '00';
          clearInterval(theTimer);
          const audio = new Audio(require('@/assets/signal.mp3'));
          audio.play();
          this.$emit('timerOff');
          this.$data.timerIsActive = false;
        }
        innerTimer++;
      }, 1000);
    },
  },
  computed: {
    nowTime() {
      return 'Hello';
    },
  },
});
