import Vue from 'vue';
import Vuex from 'vuex';
import Article from '../types/article';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    play: false,
    timerCounter: 0,
    mats: [
      {
        name: 'Вступ',
        content: 'Рух це життя!',
      },
      {
        name: 'Біологічні передумови',
        content: 'Організм людини це складна система тканин і органів',
      },
      {
        name: 'Рух - потреба організму',
        content: 'Здатність тварин до  довільного руху надала їм надзвичайні можливості для пристосування до умов зовнішнього середовища. Рухова дія була реакцією на зовнішній подразник або потребу. Впродовж тисячоліть  існування людства як біологічного виду  рухова активність була необхідною умовою виживання та існування . Завдяки м’язовим скороченням відбувалось переміщення ланок тіла одна відносно іншої та тіла в просторі.  Але крім  прямого ефекту – руху, м’язові скорочення створювали також і побічні, такі як  |зворотній нервовий зв\'язок з мозком що  впливало на мозкову діяльність,  /утворення продуктів розпаду   речовин що забезпечують енергію для скорочення м\'язів та інші.',
      },
    ],
  },
  mutations: {
    incrementCounter(state) {
      state.timerCounter = state.timerCounter + 1;
    },
    clearCounter(state) {
      state.timerCounter = 0;
    },
    playFlag(state) {
      state.play = true;
    },
  },
  actions: {
    startTimer(context, param) {
      context.commit('incrementCounter');
      const theTimer = setInterval(() => {
        context.commit('incrementCounter');
        if (context.state.timerCounter === param.cap) {
          clearInterval(theTimer), context.commit('clearCounter');
          const audio = new Audio(require('@/assets/signal.mp3'));
          audio.play();

        }
      }, 1000);
    },
  },
  modules: {
  },
});
