















import Vue from 'vue';
import Vuex from 'vuex';

// import Article from '../types/article';
// import ArticleLayout from '@/components/ArticleLayout.vue';
export default  Vue.extend({
  name: 'DropdawnSelect',
  props: {
    items: { type: Object },
    title: { type: String },
  },
  data() {
    return {
      dsTitle: this.title,
      itemsVisibility: false,
    };
  },

  methods: {
    pickItem(item: string) {
      this.dsTitle = item;
      this.itemsVisibility = false;
      this.$emit('valueIsSelected', item);
    },
    showItems() {
      this.itemsVisibility = !this.itemsVisibility;
    },
  },

  computed: {
    // dsTitle() {}
  },
});
